<template>
  <div class="NFT">
    <!-- <van-tabs v-model="activeName"
              class="titleClass">
      <van-tab title="My NFT"
               name="a">
        <mynft></mynft>
      </van-tab>
      <van-tab title="Community"
               name="b">
        <communitys></communitys>
      </van-tab>
    </van-tabs> -->
    <upLoad v-if="user && (user.type == 0 || user.type == 3)"
            @update_user="update_user"
            :user="user"></upLoad>
    <success v-else-if="user && user.type == 2"
             :user="user"></success>
    <template v-else>
      <div class="content">
        <div class="title">NFT挖矿5月1日上线</div>
        <div class="gord">
          <div class="titles">金级NFT社区申请</div>
          <div class="tips">享受10BNB可购买200W SRS创始权益</div>
          <van-button type="info"
                      :loading="btnLoading0"
                      :disabled="user && user.nftType == 0 && user.type == 1"
                      class="btnNewNFT"
                      color="#1F75D9"
                      @click="apply(0)">{{user && user.nftType == 0 && user.type == 1 ? '审核中' : '立即申请'}}
          </van-button>
        </div>
        <div class="gord">
          <div class="titles">银级NFT申请</div>
          <van-button type="info"
                      :loading="btnLoading1"
                      :disabled="user && user.nftType == 1 && user.type == 1"
                      class="btnNewNFT"
                      color="#1F75D9"
                      @click="apply(1)">{{user && user.nftType == 1 && user.type == 1 ? '审核中' : '立即申请'}}
          </van-button>
        </div>
      </div>
      <div class="address"
           v-if="adminAddress"
           @click="copy(adminAddress)">
        <div>收款地址</div>
        <div>{{adminAddress}}</div>
      </div>
    </template>
  </div>
</template>

<script>
import {
  Toast,
  Dialog
} from 'vant';
import {
  getconf,
  getsign,
  getUserInfo
} from '@/api/request.js'
import mynft from './components/NFT_my/index.vue'
import communitys from './components/NFT_community/index.vue'
import success from './components/successNFT/index.vue'
import upLoad from './components/SubmitApplicationInformation/index.vue'
export default {
  data () {
    return {
      activeName: 'a',
      sign: '',
      btnLoading0: false,
      btnLoading1: false,
      adminAddress: '',
      user: ''
    }
  },
  components: {
    mynft,
    communitys,
    upLoad,
    success
  },
  beforeRouteEnter (to, from, next) {
    if (to.path == '/NFT' && from.path == '/edit') {
      next(vm => {
        vm.activeName = 'b'
        console.log(vm)
      })
    }
    next()
  },
  computed: {
    username () {
      return this.$store.state.address;
    },
    web3 () {
      return this.$store.state.WEB3;
    }
  },
  async mounted () {
    let res = await getconf()
    this.adminAddress = res.adminAddress
    this.getUser()
  },
  methods: {
    async getUser () {
      let sign = JSON.parse(localStorage.getItem('sign'))
      if (sign && sign.address == this.username) {
        this.sign = sign.sign
        this.btnLoading0 = false
        this.btnLoading1 = false
      } else {
        try {
          this.sign = await web3.eth.personal.sign("welcome to SRS-NFT-DAO", this.username)
          console.log('地址签名', this.sign)
          let val = {
            sign: this.sign,
            address: this.username
          }
          if (this.sign) localStorage.setItem('sign', JSON.stringify(val))
        } catch (e) {
          if (this.username) {
            Dialog.confirm({
              title: '提示',
              confirmButtonColor: '#1F75D9',
              message: '请先获取签名',
            })
              .then(() => {
                this.getUser()
              })
              .catch(() => {
                // on cancel
                localStorage.removeItem('sign')
              });
          }
          console.log('error', e)
        }
      }
      let address = this.username.toLowerCase()
      if (this.sign) {
        let user = await getUserInfo(address, this.sign)
        this.user = user
        console.log('用户信息', user)
      }

    },
    update_user (val) {
      this.user = val
    },
    async apply (vals) {
      if (!this.sign) {
        this.getUser()
        return;
      }
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
      });
      this['btnLoading' + vals] = true
      let money = vals ? 1 : 10
      // 金10银1
      try {
        let hash = await web3.eth.sendTransaction({
          from: this.username,
          to: this.adminAddress,
          value: money * Math.pow(10, process.env.NODE_ENV == 'development' ? 15 : 18)
        })
        console.log('receipt', hash);
        let val = {
          address: this.username,
          txId: hash.transactionHash,
          nftType: vals
        }
        setTimeout(async () => {
          await getsign(val, this.sign)
          this.getUser()
          Toast.clear()
        }, 3000);
      } catch (e) {
        this['btnLoading' + vals] = false
        Toast.clear()
      }
    },
    copy (val) {
      let domInput = document.createElement('input');
      domInput.value = val;
      document.body.appendChild(domInput);
      domInput.select();
      document.execCommand("Copy");
      Toast(this.$t('copySuccess'))
      domInput.remove()
    }
  }
}
</script>

<style scoped lang="less">
.NFT {
  padding: 7px 10px;
  .content {
    background: #ffffff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0;
    // margin: 7px 10px;
    .gord {
      border: 1px solid #1f75d9;
      border-radius: 10px;
      box-sizing: border-box;
      padding: 27px 0;
      background: #edf5fc;
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 280px;
      .titles {
        text-align: center;
        font-size: 24px;
        color: #1f75d9;
      }
    }
    .title {
      font-size: 24px;
      font-weight: bold;
      color: #1f75d9;
    }

    .tips {
      font-size: 12px;
      margin-top: 22px;
      font-weight: 500;
      color: #00a5a3;
      transform: scale(0.9);
    }

    .btnNewNFT {
      margin-top: 20px;
      height: 32px;
      border-radius: 3px;
      width: 120px;
    }
  }

  .address {
    margin-top: 21px;
    margin-bottom: 16px;
    font-size: 12px;
    font-weight: 500;
    color: #909cba;
    display: flex;
    align-items: left;
    line-height: 1;
    flex-direction: column;

    div:nth-child(2) {
      margin-top: 5px;
    }

    div:nth-child(1) {
      flex-shrink: 0;
    }
  }
}

.custom-image /deep/.van-empty__image {
  margin-top: 104px;
  width: 113px;
  height: 117px;
}

/deep/.van-empty__description {
  line-height: 1;
  margin-top: 13px;
  font-size: 17px;
  font-weight: 400;
  color: #909cba;
}

.titleClass {
  /deep/.van-tab .van-tab__text {
    font-size: 17px;
    font-weight: 500;
    color: #909cba;
  }

  /deep/.van-tab.van-tab--active .van-tab__text {
    font-weight: bold;
    color: #1f75d9;
  }

  /deep/.van-tabs__line {
    height: 2px;
    width: 15px;
    background-color: #1f75d9;
  }

  /deep/.van-tabs__content {
    background-color: #f6f7fb;
    padding: 5px 0;
  }
}
</style>
