<template>
  <div class="SubmitApplicationInformation">
    <div class="box">
      <van-form @submit="onSubmit">
        <!-- <div class="info">
					<div class="name">地址</div>
					<van-field :rules="[{ required: true }]" required class="addressInput" v-model="username" name="username" clearable />
				</div> -->
        <div class="info unloadImages">
          <div class="name star">
            <div>场地图片</div>
            <div class="tips">场地图片最多可以上传3张</div>
          </div>
          <van-field name="field"
                     label=""
                     :rules="[{ required: true }]"
                     required>
            <template #input>
              <van-uploader :after-read="afterRead"
                            upload-text="上传图片"
                            :upload-icon="uploadIcon"
                            v-model="field"
                            accept="image/*"
                            multiple
                            max-count="3" />
            </template>
          </van-field>
        </div>

        <div class="info unloadImages">
          <div class="name star">
            <div>LOGO</div>
            <div class="tips">LOGO最多可以上传1张</div>
          </div>
          <van-field name="logo"
                     label=""
                     :rules="[{ required: true }]"
                     required>
            <template #input>
              <van-uploader :after-read="afterRead"
                            upload-text="上传图片"
                            :upload-icon="uploadIcon"
                            v-model="logo"
                            accept="image/*"
                            max-count="1" />
            </template>
          </van-field>
        </div>
        <div class="info unloadImages">
          <div class="name star">
            <div>讲师资料</div>
            <div class="tips">讲师资料最多可以上传1份，格式为图片或者.pdf、.docx、.doc、.mp4</div>
          </div>
          <van-field name="lecturer"
                     label=""
                     :rules="[{ required: true }]"
                     required>
            <template #input>
              <van-uploader :after-read="afterRead"
                            :upload-icon="uploadIcon"
                            v-model="lecturer"
                            accept="image/*,.pdf,.docx,.doc,.mp4"
                            max-count="1">
                <van-button class="selectBtn"
                            color="#1577FF"
                            type="primary">选择</van-button>
                <!-- <template #preview-cover="{ file }">
									<div class="preview-cover van-ellipsis">{{ JSON.stringify(file) }}12312</div>
								</template> -->
              </van-uploader>
            </template>
          </van-field>
        </div>
        <div class="info userPhone">
          <div class="name">邮箱</div>
          <van-field class="addressInput"
                     type="text"
                     :rules="[{ required: true }]"
                     required
                     v-model="email"
                     name="email"
                     clearable />
        </div>
        <div style="margin: 16px;">
          <van-button block
                      type="info"
                      color="#1F75D9"
                      native-type="submit">提交</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import {
  Toast
} from 'vant';
import {
  uploads
} from '@/api/request.js'
import uploadIcon from '@/assets/uploadImage.png'
export default {
  name: 'upLoad',
  props: ['user'],
  data () {
    return {
      field: [],
      logo: [],
      lecturer: [],
      email: '',
      uploadIcon: uploadIcon,
      sign: ''
    }
  },
  computed: {
    username: {
      get () {
        return this.$store.state.address;
      },
      set (val) {
        return val;
      }
    },
    web3 () {
      return this.$store.state.WEB3;
    }
  },
  mounted () {
    console.log(123123, this.user)
    // if(this.user) {
    // 	let list = []
    // 	let field = JSON.parse(this.user.imagesPath)
    // 	for(let i in field) {
    // 		list.push({
    // 			url: field[i]
    // 		})
    // 	}
    // 	this.field = list
    // 	this.logo = [{
    // 		url: this.user.logoPath
    // 	}]
    // 	this.lecturer = [{
    // 		url: this.user.docPath
    // 	}]
    // 	this.phone = this.user.phone
    // }

  },
  methods: {
    async onSubmit (values) {
      Toast.loading({
        message: '上传中...',
        duration: 0,
        forbidClick: true,
      });
      let sign = JSON.parse(localStorage.getItem('sign'))
      console.log('submit', values);
      let params = new FormData()
      if (values.logo[0].file) params.append('logo', values.logo[0].file)
      if (values.lecturer[0].file) params.append('doc', values.lecturer[0].file)
      for (let i in values.field) {
        if (values.field[i].file) params.append('images', values.field[i].file)
      }
      params.append('email', values.email)
      params.append('address', sign.address)
      params.append('sign', sign.sign)
      let res = await uploads(params)
      console.log('res', res)
      this.$emit('update_user', res)
      Toast.clear()
    },
    afterRead (file) {
      // file.status = 'uploading';
      // file.message = '上传中...';

      // setTimeout(() => {
      // 	file.status = 'failed';
      // 	file.message = '上传失败';
      // }, 1000);
    },
  }
}
</script>

<style scoped lang="less">
.SubmitApplicationInformation {
  // padding: 10px;
  // padding-top: 7px;

  .box {
    background: #ffffff;
    border-radius: 10px;
    padding: 20px 10px;

    /deep/.van-uploader__upload-text {
      line-height: 1;
    }

    .unloadImages {
      .name {
        margin-bottom: 5px !important;
      }

      .van-cell {
        padding-left: 0;
        padding-right: 0;
      }

      .selectBtn {
        width: 72px;
        height: 26px;
      }
    }

    .name {
      font-size: 14px;
      font-weight: 500;
      color: #333333;
      margin-bottom: 15px;
      flex-shrink: 0;
      position: relative;

      .tips {
        font-size: 12px;
        color: #b1b1b1;
        transform: scale(0.8);
        transform-origin: left;
      }
    }

    .star {
      margin-left: 5px;
    }

    .star::before {
      position: absolute;
      left: -8px;
      color: #ee0a24;
      font-size: 14px;
      content: "*";
    }

    .addressInput {
      height: 39px;
      background: #f6f6f6;
      border-radius: 3px;
      font-size: 12px;
      font-weight: 500;
      color: #333333;
      margin-bottom: 30px;
      align-items: center;
    }

    .userPhone {
      display: flex;
      align-items: center;
      margin-bottom: 30px;

      .name {
        margin-bottom: 0;
      }

      .addressInput {
        margin-bottom: 0;
        margin-left: 20px;
      }
    }
  }
}
</style>
