<template>
  <div class="successInfo">
    <div class="info">
      <div class="title">恭喜获得领取{{user.nftType?'银':'金'}}级NFT资格</div>
      <div class="block"
           v-if="user.nftType == 0">
        <div class="name">锁定SRS</div>
        <div class="lable">2,000,000,000</div>
      </div>
      <!-- 金级显示SRS银级USDT -->
      <!-- 6000000SRS -->
      <div class="next">任务目标：{{user.nftType?'20000USDT':'6000000SRS'}}</div>
      <!-- 金级百分比 银级USDT -->
      <div class="next">已完成：{{completes}}</div>
      <div class="block">
        <div class="name">成员</div>
        <div class="lable">{{user.childCount}}</div>
      </div>
      <div class="block">
        <div class="name">业绩</div>
        <div class="lable">{{user.performance}}</div>
      </div>
      <div class="block">
        <div class="name">5%奖励</div>
        <div class="lable">{{user.earnings}}</div>
      </div>
      <div class="titleSmall">
        <div class="tip">
          <div>任务周期还剩：</div>
          <van-count-down :time="time"
                          v-if="time > 0"
                          format="DD 天 HH 时 mm 分 ss 秒" />
        </div>

      </div>
    </div>
    <!-- 0： 未提交 1:审核中 2：审核失败  3： 审核成功 -->
    <!-- 金5000000 银100000 -->
    <van-button type="info"
                class="btnNewNFT"
                :disabled="btnState"
                @click="userExtract"
                color="#1F75D9">{{Extract}}</van-button>

  </div>
</template>

<script>
import {
  Toast
} from 'vant';
import {
  extract
} from '@/api/request.js'
export default {
  props: ['user'],
  data () {
    return {}
  },
  computed: {
    completes () {
      if (this.user.nftType) {
        return this.user.usdtPerformance
      } else {
        let num = this.user.usdtPerformance / 6000000
        if (num > 0) {
          return num.toFixed(2) + '%'
        } else {
          return 0
        }
      }
    },
    btnState () {
      // 业绩 - 债务debt
      if (this.time < 0) {
        return true
      } else {
        let all = this.user.performance - this.user.debt
        if (this.user.nftType) {
          // 银
          let state = 100000 - all
          return state > 0 ? true : false
        }
        if (this.user.nftType == 0) {
          // 金
          let state = 5000000 - all
          return state > 0 ? true : false
        }
      }
    },
    time () {
      if (this.user.updatedTime) {
        let time = new Date(this.user.updatedTime).getTime()
        let timeNow = Date.now()
        console.log(time, timeNow)
        let num = this.user.nftType ? 30 : 60
        return num * 86400000 - (timeNow - time)
      } else {
        return '-'
      }
    },
    Extract () {
      if (this.user.withdrawStatus == 1) {
        return '审核中'
      } else if (this.user.withdrawStatus == 2) {
        return '审核失败'
      } else if (this.user.withdrawStatus == 3) {
        return '审核成功'
      } else {
        return '领取'
      }
    }
  },
  methods: {
    async userExtract () {
      let sign = JSON.parse(localStorage.getItem('sign'))
      let res = await extract(sign.address.toLowerCase(), sign.sign)
      Toast(res.message)
      console.log('领取', res)
    }
  }
}
</script>

<style scoped lang="less">
.successInfo {
  .info {
    background: #ffffff;
    border-radius: 10px;
    padding: 0 6px;
    padding-top: 37px;
    .title {
      font-size: 19px;
      text-align: center;
      font-weight: bold;
      color: #1f75d9;
      margin-bottom: 20px;
    }
    .titleSmall {
      font-size: 14px;
      font-weight: bold;
      color: #1f75d9;
      padding-bottom: 20px;
      .tip {
        display: flex;
        align-items: center;
      }
    }
    .next {
      font-size: 14px;
      font-family: PingFang SC;
      font-weight: bold;
      color: #333333;
      margin-top: 15px;
      margin-bottom: 20px;
    }
    .block {
      display: flex;
      align-items: center;
      padding-bottom: 30px;
      .name {
        font-size: 14px;
        font-weight: 500;
        color: #999999;
        width: 90px;
      }
      .lable {
        font-size: 14px;
        font-weight: 500;
        color: #333333;
      }
    }
  }
  .btnNewNFT {
    margin-top: 10px;
    width: 100%;
  }
}
</style>